@media screen and (max-width: 1024px) {
  .content {
    padding-top: 0; }
  .top {
    border-radius: 0;
    background-size: cover; }
  .help-nav {
    justify-content: center; }
  .main-nav {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 7px;
    margin-right: 50px;
    &_item {
      margin: 5px 5px;
      display: block; } }
  .articles {
    display: block;
    padding: 30px 20px 20px 20px; }
  .main {
    width: 100%; }
  .slider {
    background-size: cover; }
  .main_articles {
    margin-left: 30px;
    margin-right: 30px; }
  .article-link {
    text-align: center;
    width: 90%;
    &:hover {
      transform: scale(1.3,1.3); } }
  .aside {
   width: 100%;
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   padding: 0; }
  .aside-heading {
    grid-column-start: 1;
    grid-column-end: 4; }
  .aside-article {
    margin: 15px;
    padding: 15px 18px 10px 18px; }
  .preview {
    margin: 0 auto 10px; }
  .footer {
    margin: 50px 0 0;
    display: flex;
    justify-content: center; } }

@media screen and (max-width: 870px) {
  .main_articles {
    margin-left: 0;
    margin-right: 0; } }

@media screen and (max-width: 758px) {
  .arrow {
    margin-left: 100px; }

  .main-nav {
    padding: 0;
    margin-left: 10px;
    margin-right: 10px; }

  .header {
    flex-wrap: wrap;
    justify-content: space-around; }

  .article-heading {
      flex-wrap: wrap;
      h2 {
        padding-bottom: 8px; }
      p {
       padding-left: 30px; } }

  .slider_text {
     	top: 30%;
     	width: 60%; }

  .main_articles {
   	display: flex;
   	flex-wrap: wrap;
   	align-items: stretch;
    justify-content: space-around; }

  .main_article {
    width: 40%;
    font-size: 1.5rem;
    margin: 7.5px;
    &:first-child {
      margin-left: 7.5px; }
    &:last-child {
      margin-right: 7.5px; } }


  .main_article span {
  	bottom: 15px;
  	height: 15px; }

  .articles main .big_article {
  	display: flex;
  	flex-direction: column;
  	align-items: center; }


  .articles aside span.talk {
  	padding-bottom: 20px; } }






@media screen and (max-width: 460px) {
  .articles main .article_header h2 {
    font-size: 1.2rem; }
  .articles main div.main_articles article.main_article {
    width: 80%; }
  .articles main .big_article {
    display: block;
    text-align: center; }
  .articles aside {
    display: block;
    text-align: center;
    .aside_article {
      width: 80%; } } }


