.aside-heading {
	background-color: $color-decoration;
	text-transform: uppercase;
	padding: 4px 12px 8px;
	border-radius: 3px;
	@include font-decor;
	letter-spacing: 0.15px;
	&_link {
		color: #f6f6f6;
		display: block;
		transition: all 3s;
		&:hover {
			text-align: center;
			text-decoration: underline; } } }


.aside-article {
	margin: 5px 0;
	&_heading {
		display: block;
		color: #042b38;
		@include font-main_normal;
		font-size: 12px;
		text-decoration: underline;
		padding-left: 3px;
		letter-spacing: 0;
		line-height: 1.5;
		margin-top: 2px; }
	&_text {
		margin-top: 6px;
		margin-bottom: 20px;
		color: #757575;
		@include font-main_italic;
		font-size: 11px;
		line-height: 18px;
		padding-left: 3px; }
	&_talk {
		margin-left: 12px; } }

.preview {
	width: 186px;
	height: 172px;
	display: block;
	position: relative;
	&_img {
		z-index: 10; }
	&_click {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		display: flex;
		align-items: center;
		text-align: center;
		color: white;
		font-size: 25px;
		padding: 0 15px;
		background-color: rgba(27,49,62,.5);
		z-index: -1; } }

.preview:hover {
	.preview_click {
		z-index: 1; } }



